import { Estudiante } from './../../../@core/data/models/estudiante';
import { Asignatura } from './../../../@core/data/models/asignatura';

import { EstudianteAsignatura } from './../../../@core/data/models/estudiante_asignatura';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { UbicacionesService } from '../../../@core/data/ubicaciones.service';
import { FORM_ESTUDIANTE_ASIGNATURA } from './form-estudiante_asignatura';
import { ToasterService, ToasterConfig, Toast, BodyOutputType } from 'angular2-toaster';
import { TranslateService, LangChangeEvent } from '@ngx-translate/core';
import Swal from 'sweetalert2';
import 'style-loader!angular2-toaster/toaster.css';

@Component({
  selector: 'ngx-crud-estudiante-asignatura',
  templateUrl: './crud-estudiante_asignatura.component.html',
  styleUrls: ['./crud-estudiante_asignatura.component.scss'],
})
export class CrudEstudianteAsignaturaComponent implements OnInit {
  config: ToasterConfig;
  estudiante_asignatura_id: number;

  @Input('estudiante_asignatura_id')
  set name(estudiante_asignatura_id: number) {
    this.estudiante_asignatura_id = estudiante_asignatura_id;
    this.loadEstudianteAsignatura();
  }

  @Output() eventChange = new EventEmitter();

  info_estudiante_asignatura: EstudianteAsignatura;
  formEstudianteAsignatura: any;
  regEstudianteAsignatura: any;
  clean: boolean;

  constructor(private translate: TranslateService, private ubicacionesService: UbicacionesService, private toasterService: ToasterService) {
    this.formEstudianteAsignatura = FORM_ESTUDIANTE_ASIGNATURA;
    this.construirForm();
    this.translate.onLangChange.subscribe((event: LangChangeEvent) => {
      this.construirForm();
    });
    this.loadOptionsEstudiante();
    this.loadOptionsAsignatura();
   }

  construirForm() {
    this.formEstudianteAsignatura.titulo = this.translate.instant('GLOBAL.estudiante_asignatura');
    this.formEstudianteAsignatura.btn = this.translate.instant('GLOBAL.guardar');
    for (let i = 0; i < this.formEstudianteAsignatura.campos.length; i++) {
      this.formEstudianteAsignatura.campos[i].label = this.translate.instant('GLOBAL.' + this.formEstudianteAsignatura.campos[i].label_i18n);
      this.formEstudianteAsignatura.campos[i].placeholder = this.translate.instant('GLOBAL.placeholder_' + this.formEstudianteAsignatura.campos[i].label_i18n);
    }
  }

  useLanguage(language: string) {
    this.translate.use(language);
  }

  loadOptionsEstudiante(): void {
    let estudiante: Array<any> = [];
      this.ubicacionesService.get('estudiante/?limit=0')
        .subscribe(res => {
          if (res !== null) {
            estudiante = <Array<Estudiante>>res;
          }
          this.formEstudianteAsignatura.campos[ this.getIndexForm('Estudiante') ].opciones = estudiante;
        });
  }
  loadOptionsAsignatura(): void {
    let asignatura: Array<any> = [];
      this.ubicacionesService.get('asignatura/?limit=0')
        .subscribe(res => {
          if (res !== null) {
            asignatura = <Array<Asignatura>>res;
          }
          this.formEstudianteAsignatura.campos[ this.getIndexForm('Asignatura') ].opciones = asignatura;
        });
  }

  getIndexForm(nombre: String): number {
    for (let index = 0; index < this.formEstudianteAsignatura.campos.length; index++) {
      const element = this.formEstudianteAsignatura.campos[index];
      if (element.nombre === nombre) {
        return index
      }
    }
    return 0;
  }


  public loadEstudianteAsignatura(): void {
    if (this.estudiante_asignatura_id !== undefined && this.estudiante_asignatura_id !== 0) {
      this.ubicacionesService.get('estudiante_asignatura/?query=id:' + this.estudiante_asignatura_id)
        .subscribe(res => {
          if (res !== null) {
            this.info_estudiante_asignatura = <EstudianteAsignatura>res[0];
          }
        });
    } else  {
      this.info_estudiante_asignatura = undefined;
      this.clean = !this.clean;
    }
  }

  updateEstudianteAsignatura(estudianteAsignatura: any): void {

    const opt: any = {
      title: 'Update?',
      text: 'Update EstudianteAsignatura!',
      icon: 'warning',
      buttons: true,
      dangerMode: true,
      showCancelButton: true,
    };
    Swal(opt)
    .then((willDelete) => {
      if (willDelete.value) {
        this.info_estudiante_asignatura = <EstudianteAsignatura>estudianteAsignatura;
        this.ubicacionesService.put('estudiante_asignatura', this.info_estudiante_asignatura)
          .subscribe(res => {
            this.loadEstudianteAsignatura();
            this.eventChange.emit(true);
            this.showToast('info', 'updated', 'EstudianteAsignatura updated');
          });
      }
    });
  }

  createEstudianteAsignatura(estudianteAsignatura: any): void {
    const opt: any = {
      title: 'Create?',
      text: 'Create EstudianteAsignatura!',
      icon: 'warning',
      buttons: true,
      dangerMode: true,
      showCancelButton: true,
    };
    Swal(opt)
    .then((willDelete) => {
      if (willDelete.value) {
        this.info_estudiante_asignatura = <EstudianteAsignatura>estudianteAsignatura;
        this.ubicacionesService.post('estudiante_asignatura', this.info_estudiante_asignatura)
          .subscribe(res => {
            this.info_estudiante_asignatura = <EstudianteAsignatura>res;
            this.eventChange.emit(true);
            this.showToast('info', 'created', 'EstudianteAsignatura created');
          });
      }
    });
  }

  ngOnInit() {
    this.loadEstudianteAsignatura();
  }

  validarForm(event) {
    if (event.valid) {
      if (this.info_estudiante_asignatura === undefined) {
        this.createEstudianteAsignatura(event.data.EstudianteAsignatura);
      } else {
        this.updateEstudianteAsignatura(event.data.EstudianteAsignatura);
      }
    }
  }

  private showToast(type: string, title: string, body: string) {
    this.config = new ToasterConfig({
      // 'toast-top-full-width', 'toast-bottom-full-width', 'toast-top-left', 'toast-top-center'
      positionClass: 'toast-top-center',
      timeout: 5000,  // ms
      newestOnTop: true,
      tapToDismiss: false, // hide on click
      preventDuplicates: true,
      animation: 'slideDown', // 'fade', 'flyLeft', 'flyRight', 'slideDown', 'slideUp'
      limit: 5,
    });
    const toast: Toast = {
      type: type, // 'default', 'info', 'success', 'warning', 'error'
      title: title,
      body: body,
      showCloseButton: true,
      bodyOutputType: BodyOutputType.TrustedHtml,
    };
    this.toasterService.popAsync(toast);
  }

}
