import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EstudianteAsignaturaComponent } from './estudiante_asignatura.component';
import { ListEstudianteAsignaturaComponent } from './list-estudiante_asignatura/list-estudiante_asignatura.component';
import { CrudEstudianteAsignaturaComponent } from './crud-estudiante_asignatura/crud-estudiante_asignatura.component';



const routes: Routes = [{
  path: '',
  component: EstudianteAsignaturaComponent,
  children: [{
    path: 'list-estudiante_asignatura',
    component: ListEstudianteAsignaturaComponent,
  }, {
    path: 'crud-estudiante_asignatura',
    component: CrudEstudianteAsignaturaComponent,
  }],
}];

@NgModule({
  imports: [
      RouterModule.forChild(routes),
  ],
  exports: [
      RouterModule,
  ],
})

export class EstudianteAsignaturaRoutingModule { }

export const routedComponents = [
  EstudianteAsignaturaComponent,
  ListEstudianteAsignaturaComponent,
  CrudEstudianteAsignaturaComponent,
];
